	module.exports = {"title":"React Native Setup (Ubuntu 16.04)","date":"July 16, 2016","postIntro":"Last year I started playing around with React Native. I had a tough time setting up my dev environment, and this is what I did.","body":"<p>Well, last weekend I decided that I was going to mess around a bit with React Native. (In [a previous post]({% post_url 2016-05-07-so-whats-the-plan %}), I said I wasnt going to dip into any JS frameworks until I was more comfortable with JS itself. After having heard several people say that learning React will make you a better JS developer, I thought, Hey. Two birds? One stone? Why not?) Starting a new task means setting up for a new task. And my goodness, was this a pain in the whats-it to set up. Not sure if I was just doing things wrong or if its genuinely difficult to get things up and running in my environment. In this post, Im going to talk about what all I had to do to get going. Hopefully, itll be useful for someone else  even if its just for <em>future-me</em>.</p>\n<h2>Just the Facts</h2>\n<p>If you just want a quick list of the sites on which I found all the information Im going to be discussing, here you go:</p>\n<ul>\n<li><a href=\"https://facebook.github.io/react-native/docs/getting-started.html\">Facebooks Getting Started Page</a> - I went here first.</li>\n<li>That site sends you to <a href=\"https://developer.android.com/studio/install.html\">The Android Developer Site</a> to install Android Studio. From there, youre sent:</li>\n<li>To the actual link to download <a href=\"https://developer.android.com/studio/index.html\">Android Studio</a> towell, to download Android Studio.</li>\n<li>JDK 1.8 or higher. Use WebUpd8 PPA: <a href=\"http://webupd8.org/2012/install-oracle-java-8-in-ubuntu-via-ppa.html\">Install Oracle Java 8 in Ubuntu or Linux Mint via PPA Repository [JDK8]</a></li>\n<li>Recommended (by Fb Getting Started) install: <a href=\"https://facebook.github.io/watchman/\">Watchman</a></li>\n<li>Recommended (by Fb Getting Started) install: <a href=\"https://docs.gradle.org/2.9/userguide/gradle_daemon.html\">The Gradle Daemon</a></li>\n<li>This discussion at <a href=\"http://AskUbuntu.com\">AskUbuntu.com</a>: <a href=\"https://askubuntu.com/questions/637113/unable-to-locate-package-lib32bz2-1-0\">Unable to locate package lib32bz2-1.0</a></li>\n</ul>\n<h2>The Headache</h2>\n<p><strong>NB</strong>: If youre going to follow along here, read the post completely <strong>first</strong>, as there are a few commands I discuss in the course of talking about the Headache which you should not actually bother running.</p>\n<p>So, first things first. Head on over to Facebooks Getting Started page (linked above). As long as youve got Node (4 or higher) installed, youre good. Theres an installer on the <a href=\"https://nodejs.org\">Node Website</a>. Or, if you want to use <code>nvm</code>, go to <a href=\"https://github.com/creationix/nvm\">creationixs Github page</a>, and follow the instructions to install <code>nvm</code>. Then, in your console, <code>nvm list-remote</code> to see available versions. Then <code>nvm install XXX</code>, where <code>XXX</code> is the version number you want to install. The version of <code>npm</code> that will be installed with <code>node</code> will likely not be up-to-date. So, youll want to do <code>npm install -g npm@latest</code>.</p>\n<p>NB:</p>\n<ul>\n<li><code>npm i...</code> is the same as <code>npm install</code>.</li>\n<li><code>npm i -S</code> is the same as <code>npm install --save</code></li>\n<li><code>npm i -D</code> is the same as <code>npm install --save-dev</code></li>\n</ul>\n<p>Install the React Native CLI (Command-Line Interface) by running <code>npm i -g react-native-cli</code>.</p>\n<p>Next, you want to head on over to grab <a href=\"https://developer.android.com/studio/index.html\">Android Studio</a>. The install instructions here are fairly straightforward. But this is where I got tripped up, I think. Before mucking about with Android Studio, make sure <code>javac -version</code> in your terminal returns &gt;1.8 (a version number greater than 1.8). Theres a video that demonstrates the install process on the Android Studio site. Unzip Android Studio - I put it straight into my <code>Downloads</code> folder. So theres an <code>android-studio</code> folder inside my <code>Downloads</code> folder. In your console, <code>sudo mv android-studio /usr/local</code>.</p>\n<p>Next, in your terminal, <code>export PATH=$PATH:{{the path to android-studio/bin}}</code>  for me it was <code>export PATH=$PATH:/home/my_user/android-studio/bin</code>. This makes it so that you can start Android Studio by typing <code>studio.sh</code> in your terminal (rather than having to type the whole path). The install wizard will guide you through the setup. You may need to install additional versions of the SDK depending on your development needs. I also had to install additional versions of the Build Tools. These options are available by opening Android Studio, going to Configure &gt; SDK Manager &gt; Launch Standalone SDK Manager. From here, the available packages are clearly displayed.</p>\n<p>At this point, the instructions on the Android Studio website have a note.</p>\n<p><img src=\"/src/assets/img/android-studio-note.png\" alt=\" screencap from android studio website\"></p>\n<p>It says If you are running a 64-bit version of Ubuntu, you need to install some 32-bit libraries with the following command: After which it gives a command:\n<code>sudo apt-get install lib32z1 lib32ncurses5 lib32bz2-1.0 lib32stdc++6</code>. I run this command and more than one of these packages is not located in my repository. Thankfully, theres a discussion over at <a href=\"https://askubuntu.com/questions/637113/unable-to-locate-package-lib32bz2-1-0\">AskUbuntu</a> in which Bilal says a bunch of stuff, then</p>\n<blockquote>\n<p>sudo dpkg --add-architecture i386</p>\n</blockquote>\n<blockquote>\n<p>sudo apt-get update</p>\n</blockquote>\n<blockquote>\n<p>sudo apt-get install lib32z1 lib32ncurses5 lib32bz2-1.0 libstdc++6:i386</p>\n</blockquote>\n<blockquote>\n<p></p>\n</blockquote>\n<p>The key for me was that the package <code>lib32bz2-1.0</code> is in fact no longer called <code>lib32bz2-1.0</code>, but rather <code>libbz2-1.0:i386</code>. So! <code>sudo apt install libbz2-1.0:i386</code>.</p>\n<p>After this, download <a href=\"https://facebook.github.io/watchman/\">Watchman</a> and configure <a href=\"https://docs.gradle.org/2.9/userguide/gradle_daemon.html\">The Gradle Daemon</a> by entering in your terminal:\n<code>touch ~/.gradle/gradle.properties &amp;&amp; echo &quot;org.gradle.daemon=true&quot; &gt;&gt; ~/.gradle/gradle.properties</code></p>\n<p>Now, the magic can begin. Nagivate to your project folder, in your terminal:\n<code>react-native init SuperCoolProject</code> which will create a new folder called SuperCoolProject.</p>\n<p>Then <code>cd</code> into that SuperCoolProject. Then, <code>react-native run-android</code>. Here, I got loads of errors.</p>\n<p>The various things I tried and/or had to do to get going properly were the following:</p>\n<ol>\n<li>\n<p>Run <code>react-native start</code> if the packager doesnt start automatically when you run <code>react-native run-android</code>.</p>\n</li>\n<li>\n<p>Add a file called <code>local.properties</code> to the <code>android</code> folder inside your project folder. Its contents should be a key, <code>sdk.dir</code> whose value is the path to your SDK (probably something like /home/user/Android/Sdk). So, <code>sdk.dir=/home/user/Android/Sdk</code>.</p>\n</li>\n<li>\n<p>I also added <code>/home/user/Android/Sdk</code>, <code>/home/user/Android/Sdk/tools</code>, and <code>/home/user/Android/Sdk/platform-tools</code> to my <code>__PATH__</code>. I suspect the latter two might have been redundant, but Im not certain. I added them all. One day Ill remove them one by one and see! Or, you know, you could just tell me.</p>\n</li>\n<li>\n<p><strong>This was really important:</strong> Run <code>adb reverse tcp:8081 tcp:8081</code> in your terminal before running <code>react-native run-android</code>.</p>\n</li>\n</ol>\n<p>Finally, I connected my phone. Ran <code>react-native run-android</code> and <em>voila!</em>. Theres <strong>Welcome to React Native</strong> on my phones screen. I edited <code>index.android.js</code>, shook the phone to bring up the menu, hit Reload, and Bobs your uncle. The menu also offers Live- and Hot-Reloading so that you dont have to manually reload each time you make a change.</p>\n<h2>Thats all</h2>\n<p>Well, I dont know if this will prove useful for anyone. But if nothing else, I can come back to it again later when I inevitably have to do it all over again.</p>\n<h2>Update</h2>\n<p>Since originally writing this post (though, I didnt post the post, so you wouldnt know anything about that), I installed the development build of android studio. You can change which build youre using in the Android Studio options/configuration. Running the dev version of Android Studio, I added a couple of emulators so that my project could be tested more extensively than just on my physical device. Once the emulators are setup  give them simple names (e.g. Nexus6P)  you can start them from a terminal with the command <code>emulator @Nexus6P</code>. You can see a list of what emulators you have installed with <code>emulator -list-avds</code>. Once the emulator is running, in another terminal <code>react-native start</code>, and in yet another terminal, <code>react-native run android</code>. And <em>now</em> Bobs your uncle.</p>\n<p>Please feel free to <a href=\"https://twitter.com/intent/tweet?screen_name=corderophi678\" title=\"Reach out via Twitter\">contact me via twitter</a> if youd like to discuss anything I talk about in this post. Please feel free especially if Ive gotten something wrong or done something in an inefficient way.</p>\n"}

