import React from "react";
import { Container } from "react-responsive-grid";
import { Link } from "react-router";
import { prefixLink } from "gatsby-helpers";
import Headroom from "react-headroom";
import "../css/markdown-styles";

import { rhythm } from "../utils/typography";

module.exports = React.createClass({
  propTypes() {
    return {
      children: React.PropTypes.any
    };
  },
  render() {
    return (
      <div>
        <Headroom
          wrapperStyle={{
            marginBottom: rhythm(1)
          }}
          style={{
            background: "#001B44",
            color: "#FFFCEB"
          }}
        >
          <Container
            style={{
              maxWidth: 960,
              paddingTop: 0,
              padding: `${rhythm(1)} ${rhythm(3 / 4)}`
            }}
            className="flex justify-between items-center-ns flex-column flex-row-ns"
          >
            <Link
              to={prefixLink("/")}
              className="link dim washed-yellow pv1 b f5 f4-ns"
            >
              corderophilosophy.com
            </Link>
            <Link
              to={prefixLink("/about/")}
              className="link dim washed-yellow pv1 b"
            >
              about
            </Link>
            <Link to={prefixLink("/blog/")} className="link dim washed-yellow pv1 b">
              blog
            </Link>
          </Container>
        </Headroom>
        <Container
          style={{
            maxWidth: 960,
            padding: `${rhythm(1)} ${rhythm(3 / 4)}`,
            paddingTop: 0
          }}
        >
          {this.props.children}
        </Container>
        <footer className="mw7 center">
          <div
            className="h3 flex flex-column lh-copy items-center justify-center"
          >
            <p className="fw1 f6">Thanks for visiting.</p>
          </div>
        </footer>
      </div>
    );
  }
});



