import React from "react";
import Helmet from "react-helmet";
import { config } from "config";
import { Href, HoverStyle } from "../utils/utilityClasses";

import "../css/main.css";

export default class Index extends React.Component {
  constructor() {
    super();
  }
  componentDidMount() {
    const Asteriskee = document.querySelector(".asteriskee"),
      Asterisker = document.querySelector(".asterisker");
    Asterisker.addEventListener("mouseover", () => {
      Asteriskee.classList.add("bg-gold");
    });
    Asterisker.addEventListener("mouseleave", () => {
      Asteriskee.classList.remove("bg-gold");
    });
    Asterisker.addEventListener("focus", () => {
      Asteriskee.classList.add("bg-gold");
    });
    Asterisker.addEventListener("blur", () => {
      Asteriskee.classList.remove("bg-gold");
    });
    Asterisker.addEventListener("touchstart", () => {
      Asteriskee.classList.add("bg-gold");
    });
    Asterisker.addEventListener("touchend", () => {
      Asteriskee.classList.remove("bg-gold");
    });
  }
  render() {
    return (
      <div>
        <Helmet
          title={config.siteTitle}
          meta={[
            {
              name: "description",
              content: "Dr. Richard Cordero's Personal Website. Rich Cordero is a web developer and philosopher."
            },
            {
              name: "keywords",
              content: "web development, javascript, react, philosophy, ethics"
            }
          ]}
        />
        <div className="ph0-ns ph3 pb4 mw7 center">
          <h1 className="tc mb4">Welcome!</h1>
          <article className="mh-vh-50">
            <p className="lh-copy center f5 mb3 ">
              This is my own little corner of the internet. I made it with{" "}
              <Href link="#" text="Gatsby" />
              {" "}and{" "}
              <Href link="#" text="Tachyons" />
              .
            </p>
            <p className="lh-copy center f5 mb3 ">
              Here, I'll occasionally, though I've no idea how often, be writing some words. My


              {" "}
              <HoverStyle
                href="#footnote-asterisk"
                childClass="asterisker b link underline-hover"
              >
                main objective*
              </HoverStyle>
              , as it's sort of been since November 2015, (when I decided to (slowly) begin learning various web technologies) is to be a better developer. So who knows how often and about what I'll write? We'll see...
            </p>
            <p className="lh-copy center f5 mb3 ">
              I have a Github profile that you can look at if you'd like. It's over at...well...


              <Href link="https://github.com/corderophilosophy" text="GitHub" />
              .
            </p>
            <p className="lh-copy center f5 mb3 ">
              If you'd like to get in touch, please feel free send me a tweet
              {" "}
              <Href
                link="https://twitter.com/corderophi678"
                text="@corderophi678"
              />

              .
            </p>
          </article>
          <footer className="mt4-ns tr">
            <p className="fw1 dib f6 asteriskee" id="footnote-asterisk">
              * I have no idea what this actually means.
            </p>
          </footer>
        </div>
      </div>
    );
  }
}



