import React, { Component } from "react";
import { Link } from "react-router";
import { prefixLink } from "gatsby-helpers";
import Helmet from "react-helmet";
import sortBy from "lodash/sortBy";
import get from "lodash/get";
import include from "underscore.string/include";
import { rhythm } from "../../utils/typography";
import { config } from "config";

class Index extends Component {
  render() {
    // Sort pages.
    const sortedPages = sortBy(this.props.route.pages, "data.date");
    // Posts are those with md extension that are not 404 pages OR have a date (meaning they're a react component post).
    const visiblePages = sortedPages.filter(
      page =>
        get(page, "file.ext") === "md" && !include(page.path, "/404") ||
          get(page, "data.date")
    );
    return (
      <div>
        <Helmet
          title={config.siteTitle}
          meta={[
            {
              name: "description",
              content: "Dr. Richard Cordero's Personal Blog. Rich Cordero is a web developer and philosopher."
            },
            {
              name: "keywords",
              content: "web development, javascript, react, philosophy, ethics, blog"
            }
          ]}
        />
        <article className="flex justify-center">
          <ul className="list pl0 measure">
            {visiblePages.map(page => (
              <Link key={page.path} className="link dim" to={prefixLink(page.path)}>
                <li
                  className="mw6 flex flex-column pv2"
                >
                  <div className="flex justify-between pb1">
                    {get(page, "data.title", page.path)}
                    <date>
                      {get(page, "data.date", "")}
                    </date>
                  </div>
                  <div className="measure">
                    <p className="f6 lh-copy navy">
                      {get(page, "data.postIntro", "")}
                    </p>
                  </div>
                </li>
              </Link>
            ))}
          </ul>
        </article>
      </div>
    );
  }
}

Index.propTypes = {
  route: React.PropTypes.object
};

export default Index;



