import React from "react";
import Helmet from "react-helmet";
import { config } from "config";

import { Href } from "../utils/utilityClasses";

function About(props) {
  return (
    <div>
      <Helmet
        title={config.siteTitle}
        meta={[
          {
            name: "description",
            content: "Dr. Richard Cordero's Personal Website. Rich Cordero is a web developer and philosopher."
          },
          {
            name: "keywords",
            content: "web development, javascript, react, philosophy, ethics"
          }
        ]}
      />
      <div className="ph0-ns ph3 pb4 mw7 center">
        <article className="cf pv1 pv4-ns center">
          <header className="fn fl-ns w-25-ns ph4-ns">
            <h1 className="mb3 mt0 lh-title">About:</h1>
            <h2 className="f6 ttu tracked gray">Rich Cordero</h2>
          </header>
          <div className="fn fl-ns w-75-ns">
            <p className="lh-copy measure-wide-ns mt4 mt0-ns i">
              How shall I share me? Let me count the ways:
            </p>
            <p className="lh-copy measure-wide-ns">
              The most important thing, I suppose, since it's the thing that's most often overlooked by folks at large, is that I'm a PWD -- a person with a disability. I'm also a Doctor of Philosophy. From 2006 to 2015 I was in graduate school at{" "}
              <Href
                link="http://philosophy.fsu.edu"
                text="Florida State University"
              />
              . I studied, primarily, moral philosophy and received my PhD having written a dissertation on a view called{" "}
              <Href
                link="http://pqdtopen.proquest.com/pubnum/3705795.html"
                text="moral particularism"
              />
              . I also worked in{" "}
              <Href
                link="https://plato.stanford.edu/entries/epistemology/"
                text="epistemology"
              />
              {" "}(theories of and about knowledge and related concepts),{" "}
              <Href
                link="https://plato.stanford.edu/entries/action/"
                text="philosophy of action"
              />
              {" "}(theories about what actions are, how they're generated, what it takes to perform an action) and{" "}
              <Href
                link="https://plato.stanford.edu/entries/freewill/"
                text="free will/moral responsibility"
              />
              {" "}(do we have free will? What does it take to have free will? If we don't have free will, can we be morally responsible for our actions?). It was an amazing nine years, after which I taught at{" "}
              <Href
                link="http://www.tcc.fl.edu"
                text="Tallahassee Community College"
              />
              {" "}for two semesters.
            </p>
            <p className="lh-copy measure-wide-ns">
              And that's where things take a hard left. Owing to my disability, I suffer chronic pain. It's bad enough that I'm no longer able to teach. Anticipating the decline in my condition, I started studying web development in November 2015. Since then, I've been (slowly, and as long as my body lets me) working to develop the skills necessary to get a job that I can enjoy and do from home. And I flippin' love writin' them codes.
            </p>
            <p className="lh-copy measure-wide-ns mb0-ns">
              And that's me. For now, I guess. Let's get me a job and we can change this story a bit, eh?
            </p>
          </div>
        </article>
      </div>
    </div>
  );
}

export default About;



