import React, { PropTypes } from 'react';

export function Href(props) {
  return (
    <a href={props.link} className="b link underline-hover">{props.text}</a>
  );
}

Href.propTypes = {
  link: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired
};

export function HoverStyle(props) {
  return (
    <a href={props.href} className={props.childClass}>{props.children}</a>
  );
}

HoverStyle.propTypes = { childClass: PropTypes.string };

HoverStyle.defaultProps = { childClass: 'hover-bg-light-red' };



